.cover {
  position: relative;
  width: 100%;
  /* Hibberd V. B. Kline, III Adventures-in-Time
   Navy Gray Novels of the Civil War at Sea */
  .intro {
    width: 100%;
    padding-top: 60px;
    justify-content: space-evenly;
    align-items: center;
    .title-banner {
      flex-basis: 50%;
      animation: fadeIn ease 1s;
      -webkit-animation: fadeIn ease 1s;
      -moz-animation: fadeIn ease 1s;
      -o-animation: fadeIn ease 1s;
      -ms-animation: fadeIn ease 1s;
      max-width: 600px;
      min-width: 600px;
      font-weight: bold;

      font-size: 1rem;
      @include media("<=tablet") {
        padding-top: 10%;
        min-width: 200px;
        font-size: 1rem;
      }
      @include media("<=phone") {
        min-width: 200px;
        font-size: 1rem;
      }
      .title {
        font-family: "Garamond";
        margin: 1px 0;
      }
      h3 {
        font-weight: 800;
        letter-spacing: 1px;
      }
    }
    .home-intro {
      font-family: "Times New Roman", Times, serif;
      opacity: 0;
      animation: fadeIn ease 1s forwards;
      -webkit-animation: fadeIn ease 1s forwards;
      -moz-animation: fadeIn ease 1s;
      -webkit-animation-delay: 0.5s;
      animation-delay: 0.5s;
      width: 600px;

      min-width: 400px;
      p {
        margin-bottom: 1px;
      }

      @include media("<=tablet") {
        padding-top: 10%;
        min-width: 200px;
        font-size: 1rem;
      }
      @include media("<=phone") {
        font-size: 1rem;
      }
    }
  }
  /*Book Container*/
  .book-list {
    padding-top: 20px;
    justify-content: center;
    .book-container {
      position: relative;
      background-color: rgba(255, 255, 255, 0.2);
      backdrop-filter: blur(1px);
      border-radius: 5px;
      padding-top: 5px;
      transition: box-shadow 0.3s;
      -webkit-transition: box-shadow 0.3s;
      transition: background-color 0.3s;
      -webkit-transition: background-color 0.3s;

      margin: 10px 10px;
      min-width: 200px;
      flex-basis: 30%;
      flex-shrink: 0.2;
      display: flex;
      flex-direction: column;
      @include media("<=tablet") {
        min-width: 90%;
      }
      img {
        max-width: 60%;
        margin: 0 auto;
      }
      p {
        width: 80%;
        flex-grow: 1;
        flex-basis: auto;
        margin-left: auto;
        margin-right: auto;
      }
      .learnMore {
        position: absolute;
        top: 0%;
        right: 0%;
        opacity: 0;
        margin: 0;
        padding: 10px;
        border-radius: 0 0 0 20px;
        background-color: rgba(37, 37, 37, 1);
        transition: opacity 0.3s;
        -webkit-transition: opacity 0.3s;
        @include media("<=tablet") {
          opacity: 1;
        }

        a {
          text-decoration: none;

          color: white;
        }
      }
      &:hover {
        .learnMore {
          opacity: 1;

          transition: opacity 0.3s;
          -webkit-transition: opacity 0.3s;
        }

        background: rgba(255, 255, 255, 0.6);
        box-shadow: 0px 0px 5px black;
        transition: box-shadow 0.3s;
        -webkit-transition: box-shadow 0.3s;
        transition: background-color 0.3s;
        -webkit-transition: background-color 0.3s;
      }
    }
  }
}
.buy-links {
  position: relative;
  display: flex;
  justify-content: center;
  .buy-amazon {
    flex-basis: 25%;
    flex-grow: 1;
    padding: 5px 20px;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: rgb(255, 153, 0);
    box-shadow: 0 0 5px rgba(10, 10, 10, 0.37);
  }
}

.amazon-icon {
  max-width: 40px;
  max-height: 40px;
  fill: rgb(35, 47, 62);
}
/*


KEYFRAMES



*/
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
