.cover{position:relative;width:100%}.cover .intro{width:100%;padding-top:60px;justify-content:space-evenly;align-items:center}.cover .intro .title-banner{flex-basis:50%;animation:fadeIn ease 1s;-webkit-animation:fadeIn ease 1s;-moz-animation:fadeIn ease 1s;-o-animation:fadeIn ease 1s;-ms-animation:fadeIn ease 1s;max-width:600px;min-width:600px;font-weight:bold;font-size:1rem}@media (max-width: 768px){.cover .intro .title-banner{padding-top:10%;min-width:200px;font-size:1rem}}@media (max-width: 650px){.cover .intro .title-banner{min-width:200px;font-size:1rem}}.cover .intro .title-banner .title{font-family:"Garamond";margin:1px 0}.cover .intro .title-banner h3{font-weight:800;letter-spacing:1px}.cover .intro .home-intro{font-family:"Times New Roman", Times, serif;opacity:0;animation:fadeIn ease 1s forwards;-webkit-animation:fadeIn ease 1s forwards;-moz-animation:fadeIn ease 1s;-webkit-animation-delay:0.5s;animation-delay:0.5s;width:600px;min-width:400px}.cover .intro .home-intro p{margin-bottom:1px}@media (max-width: 768px){.cover .intro .home-intro{padding-top:10%;min-width:200px;font-size:1rem}}@media (max-width: 650px){.cover .intro .home-intro{font-size:1rem}}.cover .book-list{padding-top:20px;justify-content:center}.cover .book-list .book-container{position:relative;background-color:rgba(255,255,255,0.2);-webkit-backdrop-filter:blur(1px);backdrop-filter:blur(1px);border-radius:5px;padding-top:5px;transition:box-shadow 0.3s;transition:background-color 0.3s;-webkit-transition:background-color 0.3s;margin:10px 10px;min-width:200px;flex-basis:30%;flex-shrink:0.2;display:flex;flex-direction:column}@media (max-width: 768px){.cover .book-list .book-container{min-width:90%}}.cover .book-list .book-container img{max-width:60%;margin:0 auto}.cover .book-list .book-container p{width:80%;flex-grow:1;flex-basis:auto;margin-left:auto;margin-right:auto}.cover .book-list .book-container .learnMore{position:absolute;top:0%;right:0%;opacity:0;margin:0;padding:10px;border-radius:0 0 0 20px;background-color:#252525;transition:opacity 0.3s;-webkit-transition:opacity 0.3s}@media (max-width: 768px){.cover .book-list .book-container .learnMore{opacity:1}}.cover .book-list .book-container .learnMore a{text-decoration:none;color:white}.cover .book-list .book-container:hover{background:rgba(255,255,255,0.6);box-shadow:0px 0px 5px black;transition:box-shadow 0.3s;transition:background-color 0.3s;-webkit-transition:background-color 0.3s}.cover .book-list .book-container:hover .learnMore{opacity:1;transition:opacity 0.3s;-webkit-transition:opacity 0.3s}.buy-links{position:relative;display:flex;justify-content:center}.buy-links .buy-amazon{flex-basis:25%;flex-grow:1;padding:5px 20px;margin-bottom:10px;border-radius:10px;background-color:#f90;box-shadow:0 0 5px rgba(10,10,10,0.37)}.amazon-icon{max-width:40px;max-height:40px;fill:#232f3e}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.about__cover{width:100%;margin:auto;display:flex;justify-content:space-evenly;align-items:center;background-color:rgba(238,238,238,0.719)}@media (max-width: 768px){.about__cover{padding-top:60px;flex-direction:column-reverse}}.about__cover .author-image{max-width:400px;max-height:400px;width:100%}.about__cover .blurb{max-width:600px}.about__cover .blurb p{text-indent:30px}@media (max-width: 650px){.about__cover .blurb{max-width:300px}}.about{height:100%;display:flex}body,html,#root{min-height:-webkit-fill-available;-ms-scroll-chaining:none;overscroll-behavior:none;height:100%;padding:0;overflow:auto;margin:0;font-family:"Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body ::selection,html ::selection,#root ::selection{background:rgba(85,85,85,0.5)}html{height:100;z-index:0;background-image:url("/Images/boat.png");background-size:cover;background-position:center}.text-center{text-align:center}.flex{display:flex;flex-wrap:wrap}nav{z-index:2;position:fixed;display:flex;align-items:center;justify-content:center;border-bottom:0.5px solid rgba(37,37,37,0.2);top:0;width:100%;height:50px;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(255,255,255,0.2)}@media (max-width: 650px){nav{border-radius:0 0 30px 30px;justify-content:space-evenly}}nav .NavLink{margin:0 20px;text-decoration:none;color:#212121;transition:transform 0.3s;-webkit-transition:transform 0.3s}@media (max-width: 650px){nav .NavLink{margin:0}}nav .NavLink:hover{transform:scale(1.05);transition:transform 0.3s;-webkit-transition:transform 0.3s}nav .NavLink h1{font-weight:500}nav .contact-div{position:absolute;right:5%}@media (max-width: 768px){nav .contact-div{display:none}}nav .contact-div .contactme{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}nav .contact-div p{font-family:"Roboto";margin:0;font-weight:500;color:#212121}nav .contact-div p a{margin:0;text-decoration:none;color:#913636}

